@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
* {
  box-sizing: border-box;
}

body {
  background-color: #22254b;
  color: white;
  font-family: "Poppins", sans-serif;
  margin: 0;
}

.root {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

header {
  background-color: #373b69;
  padding: 1rem;
  display: flex;
  justify-content: center;
  margin-bottom: 10px;
}

.search {
  width: 300px;
  font-family: inherit;
  font-size: 1.2rem;
  color: #fff;
  padding: 0.5rem 1.5rem;
  border-radius: 50px;
  background-color: transparent;
  border: 2px solid whitesmoke;
}

.search:focus {
  outline: none;
}

.movie-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}

button {
  display: block;
  height: 30px;
  border-radius: 5px;
  border: none;
  margin-left: 10px;
  font-size: 20px;
}

button:hover {
  transform: scale(1.05, 1.05);
  transition: transform 0.1s ease-in-out;
}

.movie {
  background-color: #373b69;
  width: 250px;
  position: relative;
  overflow: hidden;
  border-radius: 3px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.1);
  margin: 1rem;
}

.movie img {
  max-width: 100%;
  height: 400px;
  object-fit: cover;
}
.movie-info {
  display: flex;
  padding: 1rem;
  align-items: center;
  justify-content: space-between;
}

.movie-info h3 {
  margin: 0;
}

.movie-overview {
  background-color: #fff;
  position: absolute;
  padding: 1rem;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  bottom: 0;
  color: #22254b;
  left: 0;
  box-shadow: 0 -3px 5px rgba(0, 0, 0, 0.1);
  right: 0;
  overflow: auto;
  max-height: 100%;
  transform: translateY(102%);
  transition: transform 0.3s ease-in-out;
}

.movie:hover {
  transform: scale(1.05, 1.05);
  transition: transform 0.3s ease-in-out;
}

.movie:hover .movie-overview {
  transform: translate(0%);
}

span {
  background-color: #22254b;
  padding: 2px 5px 2px 5px;
}

